.users-wrap {
  display: flex;
  margin: 15px;
  justify-content: center;
  flex: 1;

  .segment {
    flex: 1;
    max-width: 33%;
    min-width: 525px;
    margin: 0;
    display: flex;
    flex-direction: column;

    .ttl-wrap {
      .icon {
        cursor: pointer;
      }
    }

    .tbl-wrap{
      overflow: auto;
    }
    .status {
      font-weight: bold;

      &.done:before {
        color: greenyellow;
        content: "Done ";
      }

      &.in-progress:before {
        color: #fbbd08;
        content: "InProgress";
      }

      &.unknown:before {
        color: #ff5f5f;
        content: "UnKnown";
      }

      &.waiting:before {
        color: #ff5f5f;
        content: "Wating";
      }


    }

    tr {
      cursor: pointer;
      position: relative;

      &.selected{

        td:first-child{
          &:before{
            background: #7e41ea !important;
            content: "";
            position: absolute;
            height: 100%;
            width: 2px;
            top: 0;
            left: 0;
          }
        }



      }
    }
  }
}
